.contact {
    width: 100%;
    min-height: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background-color: #964b00;
    padding-bottom: 30px;
}
.logoContact{
    padding: 5px;
    margin: 25px;
}
.boxDate{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.infoContact{
    max-width: 45%;
    min-width: 350px;
    height: 100%;
    text-align: center;
    margin: 0px 15px 15px 0px;
    color:#ffffff;
    font-size: 23px;
   
}
iframe {
    display: block;
    min-width: 290px;
    max-width: 49%;
    max-height: 50%;
    margin: 0px 0px 75px 0px;
    padding: 3px;
    border: blanchedalmond 3px double;
    border-radius: 15px;
}
